// main: ../style.scss
@import 'vars';
@import 'mixin'; // comportamenti predefiniti

.clearer {
	clear: both;
}

.verticalize {
	@include flex-verticalize;
}

.last-message {
	position: relative;
	display: block;
	width: 100%;
	text-align: center;
	color: $color-3;
	padding: 20px;
}

.bottom-message {
	position: fixed;
	z-index: 9999;
	bottom: 0;
	left: 0;
	display: none;
	width: 100%;
	text-align: center;
	background-color: $color-1;
	color: $color-3;
	padding: 20px;
}

.navigation {
	display: none;
}

#infscr-loading {
	position: fixed;
	z-index: 9999;
	bottom: 0;
	left: 0;
	width: calc(100% - 30px) !important;
	background-color: $color-1;
	text-align: center;
	padding: 15px;
	display: none;

	p {
		font-family: $font-main;
		font-size: $h6-size-desktop;
		line-height: $h6-lineheight-desktop;
		color: $color-4;
		text-transform: uppercase;
	}
}
// responsive iframes / videos

.video-frame {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
	overflow: hidden;
	max-width: 100%;
	height: auto;
}

.video-frame embed,
.video-frame iframe,
.video-frame object {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}